<template>
  <div class="series-course">
    <el-form
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="80px"
      class="demo-ruleForm"
    >
      <el-form-item label="课程名称" prop="name">
        <el-input
          v-model="ruleForm.name"
          maxlength="20"
          show-word-limit
          placeholder="请输入课程名称"
          :disabled="disabled"
        />
      </el-form-item>
      <el-form-item label="课程节数" prop="num">
        <el-input
          v-model.number="ruleForm.num"
          placeholder="请输入阿拉伯数字，1，2......."
          :disabled="disabled"
        />
      </el-form-item>
      <el-form-item label="章节导言" prop="summary">
        <el-input
          v-model="ruleForm.summary"
          type="textarea"
          resize="none"
          maxlength="300"
          show-word-limit
          placeholder="请输入章节导言内容"
          :disabled="disabled"
        />
      </el-form-item>
      <el-form-item label="重点内容" prop="content">
        <el-input
          v-model="ruleForm.content"
          type="textarea"
          resize="none"
          maxlength="2000"
          show-word-limit
          placeholder="请输入重点内容"
          :disabled="disabled"
        />
      </el-form-item>
      <el-form-item
        v-if="ruleForm.type !== 'catalog'"
        label="课程栏目"
        prop="type"
      >
        <el-select v-model="ruleForm.type" placeholder="请选择">
          <el-option label="企业文化" value="corporate_culture" />
          <el-option label="健康管理师职责" value="service_responsibilities" />
          <el-option label="工作流程" value="workflow" />
          <el-option label="专业能力知识" value="professional_knowledge" />
        </el-select>
      </el-form-item>
      <el-form-item label="课程封面" prop="cover">
        <el-upload
          class="avatar-uploader"
          action=""
          :show-file-list="false"
          :http-request="uploadFunc"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload"
        >
          <img
            v-if="ruleForm.cover"
            :src="ruleForm.cover"
            class="avatar"
            alt="封面"
          />
          <i v-else class="el-icon-plus avatar-uploader-icon" />
          <div slot="tip" class="el-upload__tip">
            建议尺寸600*280px，JPG、PNG格式，图片小于5M
          </div>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')"
          >确定</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { createSeries, detailCourse, editCourse } from '@/api/learningAdmin'
import { upload } from '@/api/upload'
export default {
  data() {
    return {
      ruleForm: {
        name: '',
        num: '',
        summary: '',
        content: '',
        type: '',
        cover: ''
      },
      rules: {
        name: [
          {
            required: true,
            message: '您输入的内容有误，请重新输入',
            trigger: 'blur'
          }
        ],
        num: [
          {
            required: true,
            message: '您输入的内容有误，请重新输入',
            trigger: 'blur'
          },
          { type: 'number', message: '必须为数字值' }
        ],
        summary: [
          {
            required: true,
            message: '您输入的内容有误，请重新输入',
            trigger: 'blur'
          }
        ],
        content: [
          {
            required: true,
            message: '您输入的内容有误，请重新输入',
            trigger: 'blur'
          }
        ],
        type: [
          { required: true, message: '请进行选择后再操作', trigger: 'change' }
        ],
        cover: [
          { required: true, message: '请上传封面文件', trigger: 'change' }
        ]
      },
      disabled: false,
      uploading: false
    }
  },
  async created() {
    if (this.$route.params.id) {
      const { data } = await detailCourse(this.$route.params.id)
      if (data && data.result) {
        this.disabled = data.mtsCatalog.lesson_column === 'catalog'
        this.ruleForm = {
          name: data.mtsCatalog.title,
          num: data.mtsCatalog.total_chapter,
          summary: data.mtsCatalog.summary,
          content: data.mtsCatalog.content,
          type: data.mtsCatalog.lesson_column,
          cover: data.mtsCatalog.cover
        }
      }
    }
  },
  methods: {
    async uploadFunc(option) {
      const data = await upload(option.file, progress => {})
      this.ruleForm.cover = data.url
    },
    submitForm(formName) {
      if (this.uploading) {
        this.$message({
          message: '图片/封面正在上传中不能进行提交',
          type: 'error'
        })
        return false
      }
      this.$refs[formName].validate(async valid => {
        if (valid) {
          if (this.$route.params.id) {
            const { data, code } = await editCourse(this.$route.params.id, {
              title: this.ruleForm.name,
              summary: this.ruleForm.summary,
              content: this.ruleForm.content,
              cover: this.ruleForm.cover,
              total_chapter: parseInt(this.ruleForm.num),
              lesson_column: this.ruleForm.type,
              category: 'series_lesson'
            })
            if (code === 20000 && data.result) {
              this.$message({ message: '修改成功', type: 'success' })
              this.$router.push('/learning-admin/course')
            }
          } else {
            const { data, code } = await createSeries({
              title: this.ruleForm.name,
              summary: this.ruleForm.summary,
              content: this.ruleForm.content,
              cover: this.ruleForm.cover,
              total_chapter: this.ruleForm.num,
              lesson_column: this.ruleForm.type,
              category: 'series_lesson'
            })
            if (code === 20000 && data.result) {
              this.$message({ message: '添加成功', type: 'success' })
              this.$router.push('/learning-admin/course')
            }
          }
        }
      })
    },
    handleAvatarSuccess(res, file) {
      this.uploading = false
      this.$message({ message: '上传成功', type: 'success' })
    },
    beforeAvatarUpload(file) {
      this.uploading = true
      const isJPG =
        ['image/jpeg', 'image/jpg', 'image/png'].indexOf(file.type) >= 0
      const isLt5M = file.size / 1024 / 1024 < 5
      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG或PNG 格式!')
      }
      if (!isLt5M) {
        this.$message.error('上传头像图片大小不能超过 5MB!')
      }
      return isJPG && isLt5M
    }
  }
}
</script>

<style scoped lang="scss" type="text/scss">
.series-course {
  padding: 30px 20px;
  border: 1px solid #d7dae2;
  border-radius: 2px;
  /deep/.el-input {
    width: auto;
    .el-input__inner {
      width: 450px;
    }
  }
  /deep/.el-textarea__inner {
    height: 100px;
  }
  /deep/.avatar-uploader .el-upload {
    position: relative;
    overflow: hidden;
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
  }
  /deep/.avatar-uploader .el-upload:hover {
    border-color: #409eff;
  }
  /deep/.el-upload__tip {
    line-height: 12px;
  }
}
.avatar-uploader-icon {
  width: 600px;
  height: 280px;
  line-height: 280px;
  font-size: 28px;
  text-align: center;
  color: #8c939d;
}
.avatar {
  display: block;
  width: 600px;
  height: 280px;
}
</style>
